y and proper digestion. Hay cubes are also good for your chinchilla. Not only are the nutritious for them, but they are fun for them to chew.")]),_c('h2',[_vm._v("Food Container")]),_c('p',[_vm._v("Chinchillas love to climb and sit on everything. If you use a food dish it can get knocked over easily, resulting in loss of food. The light weight of the food dish will result in it getting knocked over more frequently. Food bins that can be attached to the side of the cage are recommended, but ceramic bowls will suffice. A glass water bottle is recommended since chinchillas will chew on anything and everything, a plastic water bottle will not last very long.")]),_c('h2',[_vm._v("Chinchilla Safe Treats")]),_c('p',[_vm._v("Chinchillas should be given very little treats. Raisins and craisins are favorites, but are high in sugar. If you give your chinchillas craisins, they should be given in very small quantities and infrequently. Try not to feed more than 3 or 4 raisins per week. Rose hips are another recommended chinchilla safe treat as they are high in vitamin C and other nutrients (check your health food store). Most commercial chinchilla treats will be too high in sugar and fats, which are best avoided. There is much conflicting advice about what sort of treats are okay; check with your vet if you are unsure about anything you are feeding your chinchilla.")])])}]

